import React,{useState, useEffect} from 'react';
import alanBtn from '@alan-ai/alan-sdk-web';
import NewsCards from './components/NewsCards/NewsCards';

const alanKey='742ddb1f4ae5b3c016b10e5d65deda982e956eca572e1d8b807a3e2338fdd0dc/stage';

const App=()=>{
    const [newsArticles, setNewsArticles] = useState([]);

    useEffect(() => {
        alanBtn({
            key: alanKey,
            onCommand: ({command, articles})=>{
                if(command === 'newHeadlines'){
                    setNewsArticles(articles);
                    console.log(articles);
                }
            }
        })
    },[])
    return(
        <div>
            <h1>News Website for Blind</h1>
            <p>Try asking, Give me the news from CNN</p>
            <p>Instead of CNN you can also ask for the news from different sources.<br/>
            We provide the news from the sources like:</p>
            <ul>
                <li>  ABC News</li>
                <li>Al Jazeera English</li>
                <li>BBC News</li>
                <li>BBC Sport</li>
                <li>CBC News</li>
                <li>CNN</li>
                <li>ESPN</li>
                <li>Fox News</li>
                <li>Google News (India)</li>
                <li>National Geographic</li>
                <li>The Hindu</li>
                <li>The Times of India</li>
            </ul>
            <NewsCards articles={newsArticles}/>
        </div>
    );
}

export default App;